.header {
  z-index: 1000;
  box-sizing: border-box;
  background-color: #fdf5ee;
  justify-content: space-between;
  align-items: center;
  height: 100px;
  padding: 8px 12px;
  display: flex;
  position: sticky;
  top: 0;
  box-shadow: 0 2px 8px #00000014, inset 0 -1px #0000000a;
}

.header .logo {
  cursor: pointer;
  width: 100px;
  display: block;
}

.header .hamburger {
  cursor: pointer;
  z-index: 1001;
  display: none;
}

.header .nav-items {
  padding: 0 20px;
}

.header .nav-items ul {
  gap: 24px;
  font-size: 24px;
  list-style-type: none;
  display: flex;
}

.header .nav-items ul li {
  margin: 10px;
  padding: 10px;
}

.header .nav-items ul li a {
  color: #ff8307;
  text-decoration: none;
  transition: color .3s;
}

.header .nav-items ul li a:hover {
  color: #cc6a05;
}

@media (width <= 768px) {
  .header .nav-items {
    display: none;
  }

  .header .hamburger {
    display: block;
  }

  .header .mobile-menu {
    z-index: 1000;
    background-color: #fdf5ee;
    width: 100%;
    height: 100%;
    padding: 60px 24px;
    position: fixed;
    top: 0;
    right: 0;
    box-shadow: -2px 0 6px #0003;
  }

  .header .mobile-menu ul {
    flex-direction: column;
    gap: 20px;
    list-style: none;
    display: flex;
  }

  .header .mobile-menu ul a {
    color: #ff8307;
    font-size: 1.2rem;
    text-decoration: none;
    transition: color .3s;
  }

  .header .mobile-menu ul a:hover {
    color: #cc6a05;
  }
}

.dark .header {
  background-color: #282421;
  box-shadow: 0 2px 8px #ffffff0d, inset 0 -1px #ffffff08;
}

.dark .header .mobile-menu {
  background-color: #282421;
}

.hero-section {
